.contentCart {
    display: grid;
    column-gap: 60px;
    row-gap: 30px;
    grid-template-columns: 2fr 1fr;
    align-items: flex-start;
    grid-template-columns: repeat(auto-fit, minmax(248px, 1fr));
}

.orderBox {
    display: flex;
    flex-direction: column;
    gap: 40px;
    max-width: 461px;
    justify-self: center;
    width: 100%;
}

@media (max-width: 920px) {
    .orderBox {
    grid-column: span 2;
}
}
