.tabBarWrapper{
    background-color: #FFE44D;
    box-shadow: 0px 0px 2px rgba(40, 41, 61, 0.04), 0px -4px 8px rgba(96, 97, 112, 0.16);
    position: fixed;
    bottom: 0;
    left: 0;
    right: 0;
    margin: 0 auto;
    z-index: 9999;
}
.list {
    margin: 0;
    padding: 8px 24px;
    list-style: none;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    gap: 30px;
    
}

.item {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 4px;
    text-decoration: none;
    position: relative;
}

.title {
    font-weight: 600;
    font-size: 9px;
    line-height: 12px;
    color: #1A1A1A;
}

.image {
    width: 24px;
    height: 24px;
}

.home {
    margin-top: -160px;
}

.iconBubble {
    background-color: #01A54E;
    border: 2px solid #FFE44D;
    border-radius: 20px;
    color: #FFFFFF;
    font-size: 9px;
    line-height: 38px;
    min-width: 7px;
    height: 14px;
    display: flex;
    align-items: center;
    justify-content: center;
    box-sizing: content-box;
    position: absolute;
    left: 50%;
    top: -8px;
    padding: 1px 4px;
}
