.cartItem {
	display: grid;
	/* grid-template-columns: repeat(auto-fit, minmax(24px, max-content)); */
	/* grid-template-columns: 2fr max-content 1fr 20px; */
	grid-template-columns: 62px 1fr max-content 1fr 20px;
	gap: 32px;
	align-items: center;
	padding-top: 24px;
	padding-bottom: 24px;
	border-bottom: 1px solid #ECEFF1;
}

.cartItem:last-of-type {
	border-bottom: none;

}

.productInfo {
	display: flex;
	flex-direction: column;
	margin-left: -16px;
    
}

.cartImage {
	flex-shrink: 0;
	width: 64px;
	height: 64px;
	object-fit: contain;
	/* grid-area: image; */
}

.cartTitle {
	/* grid-area: title; */
	margin: 0;
	font-weight: 700;
	font-size: 14px;
	line-height: 20px;
	max-width: 244px;
}

.cartPrice {
	text-align: right;
}

.cartWeight {
	grid-area: weight;
	margin: 0;
	font-weight: 400;
	font-size: 12px;
	line-height: 14px;
	color: #7B8E98;
}

.btnTrash {
	padding: 0;
	border: none;
	background-color: transparent;
	cursor: pointer;
}

.link {
	text-decoration: none;
	color: inherit;
}

@media (max-width: 480px) {
	.cartItem{
		grid-template-columns: 62px 1fr max-content 1fr 20px;
		align-items: start;
		grid-template-rows: max-content 1fr max-content;
		grid-template-columns: 68px 1fr 20px;
		justify-items: start;
		column-gap: 16px;
		row-gap: 8px;
	}
	.cartImage{
		grid-row: 1 / 3;
	}
	.productInfo{
		order: 4;
    	grid-column: 2 / 4;
		gap: 8px;
		margin-left: 0;
	}
	.cartPrice{
		order: 1;
		text-align: left;
	}
	.btnTrash{
		order: 3;
	}
	
}