.deliveryInfo{
grid-column: -1 / -2;
}

.delivery {
    display: flex;
    background-color: rgba(236, 239, 241, 0.6);
    border-radius: 12px;
    padding: 12px;
    align-items: flex-start;
}

.delivery:not(.delivery:last-child) {
    margin-bottom: 8px;
}

.right {
    margin-left: 12px;
}

.name {
    font-size: 16px;
    line-height: 20px;
    font-weight: 700;
    margin-bottom: 8px;
    margin-top: 0;
}

.text {
    font-size: 14px;
    line-height: 20px;
    font-weight: 400;
}

.text:not(.text:last-child) {
    margin-bottom: 8px;
}


@media (max-width: 920px) {
    .deliveryInfo{
        grid-column: span 2;
    }
}