.menu {
    padding: 20px 40px;
    background-color: #FFFFFF;
}

.list {
    margin: 0;
    padding: 0;
    list-style: none;
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    gap: 30px;
}

.item {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 20px;
    text-decoration: none;
}

.title {
    font-weight: 700;
    font-size: 16px;
    line-height: 20px;
    color: #1A1A1A;
}

.image {
    width: 46px;
    height: 46px;
}

.home {
    margin-top: -160px;
}


@media (max-width: 768px) {
    .list {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
    }
    .menu {
        padding: 16px 24px;        
    }
    .title{
        font-size: 14px;
    }
}