.header {
    width: 100%;
    background-color: #FFE44D;
    padding: 12px 0;
  }
  
  .header__wrapper {
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 80px;
  }
  
.header__userInfo{
  display: flex;
  flex-direction: column;
}

.profile {
  display: flex;
  flex-direction: column;
  font-size: 18px;
  font-weight: 600;
  align-items: flex-end;
}

.iconsMenu {
  display: flex;
  flex-wrap: wrap;
  gap: 30px;
}

.favoritesLink {
  position: relative;
  width: 34px;
  height: 34px;
}

.iconsMenu svg {
  width: 100%;
  height: 100%;
}

.iconBubble {
  background-color: #01A54E;
  border: 2px solid #FFE44D;
  border-radius: 50%;
  color: #FFFFFF;
  font-size: 14px;
  width: 22px;
  height: 22px;
  line-height: 1.25;
  display: flex;
  align-items: center;
  justify-content: center;
  box-sizing: border-box;
  position: absolute;
  bottom: 60%;
  left: 70%;
}

.iconsMenuItem {
  display: flex;
  font-size: 18px;
  font-weight: 600;
  align-items: center;
  gap: 10px;
  text-decoration: none;
  color: #1A1A1A;
}

.iconsMenuItem svg {
  width: 28px;
  height: 28px;
  flex-shrink: 0;
  fill: currentColor;
}

  @media (max-width: 480px) {
    .header__wrapper {
      align-items: stretch;
      gap: 20px;
    }
    .iconsMenu {
      gap: 20px;
    }
  }

