.banner {
    background-color: #FFE44D;
    padding-top: 40px;
    padding-bottom: 240px;
    clip-path: polygon(0% -1%, 100% -1%, 100% 100%, 0 83%);
    overflow: hidden;
}

.container {
    /* background-image: url('./img/banner.png');
    background-position: right top 44%;
    background-repeat: no-repeat; */

    display: flex;
    
    
}

.title {
    font-weight: 900;
    font-size: 48px;
    line-height: 52px;
    max-width: 425px;
}

.subtitle {
    font-weight: 300;
    font-size: 24px;
    line-height: 28px;
    max-width: 425px;
}

.bannerImage{
    align-self: end;
    min-width: 390px;
    flex: 1;
    display: grid;
}
.bannerImage img{
    
    justify-self: end;
    transform: translateY(25%);
}


@media (max-width: 768px ) {
    .container {
        flex-wrap: wrap;
    }
    .banner {
        clip-path: none;
    }
    .bannerImage img{    
        transform: translateY(0);
    }
    
}
@media (max-width: 480px ) {
    .title {
        font-size: 38px;
        line-height: 40px;
        max-width: 425px;
    }
    .subtitle {
        font-size: 19px;
        line-height: 24px;
    }
    .bannerImage {
        transform: translate(15%, 0);
        min-width: 340px;
    }
    
}